import fs from "fs/promises";
import path from "path";
import { promisify } from "util";
import { gzip as zlibGzip } from "zlib";

const gzip = promisify(zlibGzip);

export interface FileItem {
  id: string;
  name: string;
  originalName: string;
  size: number;
  compressedSize: number;
  type: string;
  folderId: string | null;
  createdAt: Date;
  data?: string; // base64 compressed data (optional now)
}

export interface Folder {
  id: string;
  name: string;
  parentId: string | null;
  createdAt: Date;
}

const ROOT_DIR = path.join(process.cwd(), "uploads");

// Ensure root directory exists
const ensureRoot = async () => {
  try {
    await fs.access(ROOT_DIR);
  } catch {
    await fs.mkdir(ROOT_DIR, { recursive: true });
  }
};

ensureRoot();

export async function getFiles(folderId: string | null): Promise<FileItem[]> {
  await ensureRoot();
  const targetDir = folderId ? path.join(ROOT_DIR, folderId) : ROOT_DIR;

  try {
    const entries = await fs.readdir(targetDir, { withFileTypes: true });
    const files: FileItem[] = [];

    for (const entry of entries) {
      if (entry.isFile() && entry.name.endsWith(".json")) {
        const content = await fs.readFile(
          path.join(targetDir, entry.name),
          "utf-8"
        );
        try {
          const fileData = JSON.parse(content);
          // Ensure id is relative path from ROOT_DIR
          const relativePath = path.relative(
            ROOT_DIR,
            path.join(targetDir, entry.name.replace(".json", ""))
          );
          files.push({ ...fileData, id: relativePath });
        } catch (e) {
          console.error(`Error parsing file metadata: ${entry.name}`, e);
        }
      }
    }
    return files;
  } catch (e) {
    console.error("Error reading directory:", e);
    return [];
  }
}

export async function getAllFiles(): Promise<FileItem[]> {
  return getFiles(null);
}

export async function getFile(id: string): Promise<FileItem | undefined> {
  await ensureRoot();
  const metaPath = path.join(ROOT_DIR, id + ".json");
  try {
    const content = await fs.readFile(metaPath, "utf-8");
    const fileData = JSON.parse(content);
    return { ...fileData, id };
  } catch {
    return undefined;
  }
}

export async function addFile(file: FileItem, buffer: Buffer): Promise<void> {
  await ensureRoot();
  const targetDir = file.folderId
    ? path.join(ROOT_DIR, file.folderId)
    : ROOT_DIR;

  // Ensure target directory exists
  try {
    await fs.access(targetDir);
  } catch {
    await fs.mkdir(targetDir, { recursive: true });
  }

  // Compress data
  const compressedBuffer = await gzip(buffer);
  file.compressedSize = compressedBuffer.length;

  const filePath = path.join(targetDir, file.name);
  const metaPath = path.join(targetDir, file.name + ".json");

  await fs.writeFile(filePath, compressedBuffer);
  await fs.writeFile(metaPath, JSON.stringify(file, null, 2));
}

export async function deleteFile(id: string): Promise<boolean> {
  await ensureRoot();
  const filePath = path.join(ROOT_DIR, id);
  const metaPath = path.join(ROOT_DIR, id + ".json");

  try {
    await fs.unlink(filePath);
    await fs.unlink(metaPath);
    return true;
  } catch {
    return false;
  }
}

export async function getFolders(parentId: string | null): Promise<Folder[]> {
  await ensureRoot();
  const targetDir = parentId ? path.join(ROOT_DIR, parentId) : ROOT_DIR;

  try {
    const entries = await fs.readdir(targetDir, { withFileTypes: true });
    const folders: Folder[] = [];

    for (const entry of entries) {
      if (entry.isDirectory()) {
        const folderPath = path.join(targetDir, entry.name);
        const relativePath = path.relative(ROOT_DIR, folderPath);
        folders.push({
          id: relativePath,
          name: entry.name,
          parentId: parentId,
          createdAt: new Date(),
        });
      }
    }
    return folders;
  } catch {
    return [];
  }
}

export async function getFolder(id: string): Promise<Folder | undefined> {
  await ensureRoot();
  const folderPath = path.join(ROOT_DIR, id);
  try {
    await fs.access(folderPath);
    return {
      id,
      name: path.basename(id),
      parentId: path.dirname(id) === "." ? null : path.dirname(id),
      createdAt: new Date(),
    };
  } catch {
    return undefined;
  }
}

export async function addFolder(folder: Folder): Promise<void> {
  await ensureRoot();
  const targetDir = folder.parentId
    ? path.join(ROOT_DIR, folder.parentId, folder.name)
    : path.join(ROOT_DIR, folder.name);
  await fs.mkdir(targetDir, { recursive: true });
}

export async function deleteFolder(id: string): Promise<boolean> {
  await ensureRoot();
  const folderPath = path.join(ROOT_DIR, id);
  try {
    await fs.rm(folderPath, { recursive: true, force: true });
    return true;
  } catch {
    return false;
  }
}

export async function getBreadcrumbs(folderId: string): Promise<Folder[]> {
  const breadcrumbs: Folder[] = [];
  let currentId: string | null = folderId;

  while (currentId) {
    const folder = await getFolder(currentId);
    if (folder) {
      breadcrumbs.unshift(folder);
      currentId = folder.parentId;
    } else {
      break;
    }
  }

  return breadcrumbs;
}

export async function searchFiles(query: string): Promise<FileItem[]> {
  await ensureRoot();
  const results: FileItem[] = [];

  async function searchDir(dir: string) {
    const entries = await fs.readdir(dir, { withFileTypes: true });

    for (const entry of entries) {
      const fullPath = path.join(dir, entry.name);
      if (entry.isDirectory()) {
        await searchDir(fullPath);
      } else if (entry.isFile() && entry.name.endsWith(".json")) {
        try {
          const content = await fs.readFile(fullPath, "utf-8");
          const fileData = JSON.parse(content);
          if (fileData.name.toLowerCase().includes(query.toLowerCase())) {
            const relativePath = path.relative(
              ROOT_DIR,
              fullPath.replace(".json", "")
            );
            results.push({ ...fileData, id: relativePath });
          }
        } catch (e) {
          console.error(
            `Error parsing file metadata during search: ${entry.name}`,
            e
          );
        }
      }
    }
  }

  await searchDir(ROOT_DIR);
  return results;
}
